@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.confirm{position:fixed;top:50%;left:50%;transform:translate(-50%, -50%);box-shadow:0 0 6px gray;padding:.5rem 1.5rem;border-radius:.25rem;background-color:white;text-align:center;z-index:1000}.confirm p{padding:.5rem}*{margin:0;padding:0;box-sizing:border-box;font-size:1em}a{text-decoration:none;font-family:"Roboto",sans-serif;color:black;cursor:pointer}input,select{font-family:"Roboto",sans-serif;outline:none;padding:.25rem .5rem}input[type=checkbox]{cursor:pointer}.checkbox-element{position:relative;height:1rem;width:1rem;align-self:center;justify-self:center}.checkbox-element input[type=checkbox]{opacity:0}.checkbox-element input[type=checkbox]:checked ~ label>.checked{background:#4FA825}.checkbox-element label{pointer-events:none;position:absolute;top:0;left:0;width:1rem;height:1rem;box-shadow:inset 0 0 2px #727272;border-radius:3px;background-color:white}.checkbox-element .checked{position:absolute;background:white;border-radius:3px;height:.55rem;width:.55rem;left:50%;top:50%;transform:translate(-50%, -50%)}h2{font-size:2.625rem;font-family:"Open Sans",sans-serif;margin-bottom:2rem}h3{font-size:1.25rem;font-family:"Open Sans",sans-serif}body{box-sizing:inherit;font-family:"Roboto",sans-serif;overflow-x:hidden}.buttons{display:flex;flex-direction:row}.buttons .button+.button{margin-left:1rem}.button{outline:none;background:none;border:none;cursor:pointer;transition:.2s}.button--add{display:flex;flex-direction:row;color:white;font-weight:bold;background-color:#2ED667;padding:.75rem}.button--add img{padding-right:1rem}.button--add:active{transform:scale(0.95)}.button--edit{background-color:#FFCF52}.button--save{background-color:#55CCD3}.button--save,.button--edit{display:flex;flex-direction:row;color:white;font-weight:bold;height:2rem;padding:0 .5rem;align-items:center;justify-items:center;margin:0 auto .5rem}.button--save img,.button--edit img{height:1rem;padding-right:1rem}.button--delete{display:flex;flex-direction:row;color:white;font-weight:bold;background-color:#FF7777;padding:.75rem}.button--delete:active{transform:scale(0.95)}.button--delete img{padding-right:1rem}.button--addItem{height:3rem;display:flex;flex-direction:row;font-size:1.25rem;font-weight:bold;background-color:#55CCD3;color:white;align-items:center;align-self:flex-end}.button--addItem img{margin:0 1rem}.button--addItem p{padding-right:1rem}.button--confirm{padding:.25rem 1.5rem;margin:.5rem 1rem .25rem 0;background-color:#2ED667;color:white}.button--reject{padding:.25rem 1.5rem;margin:.5rem 0 .25rem 1rem;background-color:#FF7777;color:white}.button--clear{background-color:#FF7777}.button--search{background-color:#2ED667}.button--clear,.button--search{display:flex;flex-direction:row;align-items:center;color:white;font-weight:bold;padding:.5rem .75rem;margin:.5rem 0}.button--clear p,.button--search p{padding-left:1rem}.button--clear:active,.button--search:active{transform:scale(0.95)}.App{min-height:100vh;width:100vw}.App .wrapper{width:100%;margin:0 auto;display:flex;flex-direction:row}.App .content{padding:1rem 2.5rem 1.5rem 2rem;background-color:transparent;flex:1 1}.empty{margin:1rem auto;color:gray}

nav{display:flex;flex-direction:column;background-color:white;min-height:100vh;box-shadow:0 0 10px rgba(0,0,0,0.25)}nav a{padding:1.5rem 0;background-color:white;font-size:1.5rem;display:grid;grid-template-columns:3fr 1fr;align-items:center;color:black;text-align:left}nav a:focus{background:#E6E5FF}nav a p{padding:0 2.5rem}nav a img{justify-self:center;padding-right:1.5rem}

.form{display:flex;flex-direction:row;background-color:white;padding:1rem;margin:1rem auto;box-shadow:0 0 10px rgba(0,0,0,0.25);justify-content:space-between}.form__element{display:flex;flex-direction:column}.form__element label{font-size:1.25rem;font-weight:bold;padding-bottom:.75rem}.form__element select{width:10rem}.form .name--error{border:2px solid red}

.filter{display:flex;flex-direction:row;border-top:1px solid #282828;border-bottom:1px solid #282828;margin-bottom:1rem}.filter label{font-weight:bold;font-size:1.25rem;padding:.5rem 0}.filter__item{margin:.5rem 0;padding:1rem;display:flex;flex-direction:column}.filter__item+.filter__item{margin-left:1rem}.filter__item .buttons{display:flex;flex-direction:row;justify-content:space-between}

.confirm{position:fixed;top:50%;left:50%;transform:translate(-50%, -50%);box-shadow:0 0 6px gray;padding:.5rem 1.5rem;border-radius:.25rem;background-color:white;text-align:center;z-index:1000}.confirm p{padding:.5rem}

.expense{display:grid;grid-template-columns:2rem repeat(6, 1fr);width:100%;align-items:center;justify-items:center;border-bottom:1px solid #282828}.expense input,.expense select,.expense option{width:80%}.expense p{padding:.75rem}.expense .button--edit p,.expense .button--save p{padding:.25rem;display:block}.expense--chosen{background-color:#E6E5FF}

.history{width:100%}.history__buttons{display:flex;flex-direction:row}.history__buttons .button{margin:0 0 2rem 0}.history__buttons .button+.button{margin-left:1.25rem}.history .table__header{color:white;font-weight:bold;font-size:1.25rem;background-color:#8D57FF;display:grid;grid-template-columns:2rem repeat(6, 1fr);width:100%}.history .table__header input{align-self:center;justify-self:center}.history .table__header__item{display:flex;flex-direction:row;text-align:center;padding:.25rem 0}.history .table__header__item p{margin:0 auto}.history .table__header p{padding:.5rem}

.category-name{border:1px solid #282828}.category-name--error{border:1px solid red}.category{display:grid;grid-template-columns:3fr 1fr;box-shadow:0 0 6px gray;justify-content:center;align-content:center;margin:1rem 0;padding:.65rem;width:100%;transition:.2s;cursor:pointer}.category:hover{transform:scale(1.1)}.category .button{align-self:end;justify-self:end;display:flex;flex-direction:row;justify-items:center;align-items:center}

.categories{width:15rem}.categories h3{padding-bottom:1rem}.categories .button--add{padding:0.5rem;margin-left:1rem}.categories .button--add p{padding:0}.categories .button--delete{padding:0.5rem}.categories .button--delete p{padding:0}.categories__form{display:grid;grid-template-columns:3fr 1fr}.categories__form img{height:1.25rem}

.analytics__wrapper>h3{font-size:1.5rem;margin-top:1rem}.analytics__info{display:flex;flex-direction:column;margin-top:1rem}.analytics__info__item{padding:1rem 0;border-bottom:1px solid #282828}.analytics__info__item h3{padding-bottom:1rem}.analytics .biggest-spending{display:grid;grid-template-columns:1fr 2fr;width:20rem}.analytics .biggest-spending p{font-weight:bold}.analytics .biggest-spending p,.analytics .biggest-spending span{padding:.25rem}.analytics__categories{display:flex;flex-direction:row;align-items:flex-end;text-align:center;justify-content:center}.analytics__categories>div{margin:0 1rem}.analytics__categories>div p{padding-bottom:.5rem}.analytics__categories>div h5{padding-top:.5rem;font-weight:lighter}.analytics__categories .column{margin:0 auto}

